<template>
  <div class="toCoupons">
    <van-nav-bar
      fixed
      @click-left="$toPage(-1)"
      @click-right="$toPage('lineUnderCoupons')"
    >
      <van-icon name="arrow-left" size="24px" color="#fff" slot="left" />
      <span slot="right" style="font-size: 15px">线下优惠券</span>
    </van-nav-bar>
    <div class="logo">
      <img src="..\..\assets\me_icon\u990.png" alt="尽享优惠  省钱有诀窍" />
      <div class="font">领券中心</div>
      <div class="font-second">领券享优惠 省钱有诀窍</div>
    </div>
    <conpous :coupons="coupons" @couponBtn="couponBtn" />
    <div v-if="flag" class="bgImg"></div>
  </div>
</template>
<script>
import conpous from "@/components/couponsAssembly.vue";
export default {
  data() {
    return {
      coupons: [],
      reduce: "",
      symbol: "",
      flag: true
    };
  },
  components: {
    conpous
  },
  mounted() {
    if((this.$loadUserInfo().comp.authenticationStatus & 256) > 0){
      this.getCoupons()
    }
  },
  methods: {
    couponBtn(data) {
      this.getCoupons()
    },
    getCoupons() {
      JNB.query({
        serverName: "discountServer",
        cls: "CouponManageModule",
        method: "queryCouponPub",
        params: {
          compid: this.$loadUserInfo().comp.storeId,
          gcode: -1
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            this.coupons = result.data;
            if (result.data.length > 0) {
              this.flag = false;
            }
            this.coupons.forEach(data => {
              data.btn = "立即领取";
              if (data.brulecode === 2110) {
                data.reduce = "减";
                data.symbol = "元";
              } else if (data.brulecode === 2120) {
                data.reduce = "包邮";
                // if (data.ladderVOS[0].offer === 0) {
                data.ladderVOS[0].offer = "";
                // }
              } else {
                data.reduce = "";
                data.symbol = "折";
              }
            });
            if (this.coupons.length === 0) {
              // this.$toast('此页面没有优惠券哦！')
            }
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    }
  }
};
</script>
<style scoped>

* {
  margin: 0;
  padding: 0;
}
.toCoupons .van-nav-bar {
  background-color: rgba(255, 255, 255, 0);
  color: #fff;
}
.toCoupons .logo {
  width: 100%;
  height: 400px;
}
.toCoupons .logo img {
  width: 100%;
  height: 100%;
}
.toCoupons .main {
  width: 715px;
  height: 152px;
  overflow: hidden;
  margin: 20px auto;
}
.toCoupons .main .title {
  width: 230px;
  height: 152px;
  background: url("../../assets/me_icon/coupon.png") no-repeat;
  background-size: contain;
  font-size: 32px;
  font-weight: 400;
  color: rgba(255, 255, 255, 1);
  text-align: center;
  line-height: 152px;
  float: left;
}
.toCoupons .main .content {
  width: 480px;
  height: 148px;
  border: 1px solid rgba(230, 230, 230, 1);
  border-left: none;
  float: left;
}
.toCoupons .main .content .couponsText {
  font-size: 28px;
  width: 330px;
  height: 100%;
  float: left;
  margin: 10px 0;
  position: relative;
}
.toCoupons .main .content .couponsText p {
  margin: 10px 0 0 30px;
}
.toCoupons .main .content .couponsText .time {
  position: absolute;
  bottom: 20px;
  font-size: 20px;
  color: rgba(153, 153, 153, 1);
}
.toCoupons .main .content .btn {
  width: 150px;
  height: 152px;
  float: left;
  text-align: center;
  line-height: 152px;
  font-size: 28px;
  color: rgba(248, 134, 57, 1);
}
.van-nav-bar__text {
  color: #ffffff;
}
.van-nav-bar .van-icon {
  color: #ffffff;
}
.van-hairline--bottom:after {
  border-bottom: 0px;
}
.font {
  position: absolute;
  font-size: 65px;
  color: #ffffff;
  left: 260px;
  top: 90px;
}
.font-second {
  position: absolute;
  font-size: 24px;
  color: #ffffff;
  left: 260px;
  top: 210px;
  font-family: PingFang-SC-Regular;
}
.toCoupons .bgImg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: url("~@\assets\noneImg\youhui.png") no-repeat;
  background-position: center center;
  background-size: 50%;
}
</style>
